import React, {Component} from 'react';
import PropTypes from 'prop-types';
import FacebookLogin from 'react-facebook-login';

/**
 * ExampleComponent is an example component.
 * It takes a property, `label`, and
 * displays it.
 * It renders an input with the property `value`
 * which is editable by the user.
 */
export default class Dashforfaceauth extends Component {
    render() {
      const {id, label, appId, nameu, setProps, n_clicks, resultFacebookLogin} = this.props;

        return (
          <div id={id}>
              ExampleComponent: {label}&nbsp;
              <FacebookLogin
                      appId= {appId}
                      autoLoad={false}
                      fields="name, picture, email"
                      callback={responseFacebook => setProps({ resultFacebookLogin: responseFacebook }) }
                      cssClass="my-facebook-button-class"
                      icon="fa-facebook"
                    //  onClick={
                          /*
                           * Send the new value to the parent component.
                           * setProps is a prop that is automatically supplied
                           * by dash's front-end ("dash-renderer").
                           * In a Dash app, this will update the component's
                           * props and send the data back to the Python Dash
                           * app server if a callback uses the modified prop as
                           * Input or State.
                           */
                    //    e => setProps({ outcome:  e.target.callback})
                    //}
                    />

            </div>
        );
    }
}

Dashforfaceauth.defaultProps = {};

Dashforfaceauth.propTypes = {
  /**
  * The ID of this component, used to identify dash components
  * in callbacks. The ID needs to be unique across all of the
  * components in an app.
  */
  id: PropTypes.string,
  /**
  * label for testing
  */
  label: PropTypes.string,
  /**
  * The Identification of the APP given by Facebook for developers
  */
  appId: PropTypes.string,
  /**
  * name extracted
  */
  nameu: PropTypes.string,
  /**
  * Dash-assigned callback that should be called whenever any of the
  * properties change
  */
  setProps: PropTypes.func,
  /**
  * property to identify when is clicked
  */
  n_clicks: PropTypes.oneOf(['change']),
  /**
  * the outcome of the callback
  */
  resultFacebookLogin: PropTypes.object
};
